import axios from 'axios';
import Config from '../config/config';

const Apiurl = Config.apiUrl;
const headers = {
    'Content-Type': 'application/json',
    'Access-Control-Allow-Origin': '*'
};
const HttpService = {
    register(data) {
        return axios.post(`${Apiurl}vendorLogReg/signup`, data);
    },
    vendorLogin(data) {
        return axios.post(`${Apiurl}vendorLogReg/login`, data);
    },
    vendorForgotPassword(data) {
        return axios.post(`${Apiurl}vendorLogReg/forgot-password`, data);
    },
    setNewPassword(data) {
        return axios.post(`${Apiurl}vendorLogReg/set-password`, data);
    },
    getVendorProfileDetail() {
        return axios.get(`${Apiurl}vendorLogReg/loggedVendor`);
    },
    vendorProfileUpdate(data) {
        return axios.put(`${Apiurl}vendors/editVendorProfile`, data);
    },
    getAllShippingOrderData() {
        return axios.get(`${Apiurl}shippingOrders/all`);
    },
    getshippingOrderViewDetails(orderid) {
        return axios.get(`${Apiurl}shippingOrders/getShippingOrderById/${orderid}`);
    },
    checkPinCodes(pincode) {
        return axios.get(`https://api.postalpincode.in/pincode/${pincode}`, { headers });
    },
    createShippingOrder(orderData) {
        return axios.post(`${Apiurl}shippingOrders/new`, orderData);
    },
    cloneShippingOrder(orderId) {
        return axios.put(`${Apiurl}shippingOrders/cloneShippingOrder/${orderId}`);
    },
    cancelShippingOrder(orderId) {
        return axios.put(`${Apiurl}shippingOrders/cancelShippingOrder/${orderId}`);
    },
    updateShippingOrder(orderId, orderData) {
        return axios.put(`${Apiurl}shippingOrders/editShippingOrder/${orderId}`, orderData);
    }
};

export default HttpService;
